.game-cell {
  align-items: center;
  display: grid;
  font-weight: bold;
  justify-content: center;
  /* border: 1px #949494 solid; */
  padding: 0;
  margin: 0;
  position: relative;
}

.game-cell > .btn {
  padding: 0;
}

.game-btn {
  width: 100%;
  height: 100%;
  border-radius: 0;
  position: absolute;
  opacity: 0.7;
  border-radius: 5px;
  transition: all 0.3s ease;
}
